@import "https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400&family=Montserrat:wght@200;400;600;800&display=swap";
html, body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
  overflow-x: hidden;
}

div#main {
  width: 100vw;
  flex-direction: column;
  display: flex;
}

section {
  width: 100vw;
  position: relative;
}

section.min-height {
  min-height: 100vh;
}

section.padding {
  padding: 5rem 0;
}

section img.background {
  width: 100vw;
  height: 100vh;
  object-fit: cover;
  z-index: -1;
  pointer-events: none;
  opacity: 1;
  filter: none;
  transition: opacity .3s, filter .3s;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

section#about img.background {
  filter: brightness(.85);
  opacity: .95;
}

section#about > div {
  background: #ffffff85;
}

h1 {
  margin: 0;
  font-family: Cormorant Garamond, serif;
  font-size: 7rem;
  font-weight: 400;
  line-height: .8;
}

h2 {
  margin: 0;
  padding-top: 1rem;
  font-size: 2rem;
  font-weight: 400;
}

h3 {
  margin: 0;
  font-size: 1.25rem;
  font-weight: 400;
}

p {
  margin-bottom: 0;
  font-size: 18px;
  font-weight: 300;
  line-height: 1.5;
}

p.large {
  font-size: 24px;
}

p.small {
  font-size: 15px;
}

hr {
  margin: 2.5rem;
}

li {
  line-height: 1.7;
}

li::marker {
  color: #4e567e;
}

a {
  color: #000;
  text-decoration: none;
}

a:hover {
  color: #4e567e;
}

.portfolio-grid {
  grid-gap: 32px;
  width: 100%;
  max-width: 900px;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  margin: 32px auto;
  display: grid;
}

.project-card {
  background: var(--card-bg, #fff);
  text-align: center;
  border: 1px solid #e0e0e0;
  border-radius: 16px;
  flex-direction: column;
  align-items: center;
  padding: 24px 16px 16px;
  transition: transform .2s, box-shadow .2s;
  display: flex;
  box-shadow: 0 2px 8px #0000000a;
}

.project-card:hover {
  transform: translateY(-6px)scale(1.03);
  box-shadow: 0 8px 24px #4e567e1f;
}

.project-image {
  width: 100%;
  max-width: 220px;
  height: 120px;
  object-fit: cover;
  background: #f5f5f5;
  border-radius: 10px;
  margin-bottom: 1rem;
}

.tech-list {
  flex-wrap: wrap;
  justify-content: center;
  gap: .5rem;
  margin: 1rem 0 .5rem;
  display: flex;
}

.tech-badge {
  color: #fff;
  letter-spacing: .02em;
  background: #4e567e;
  border-radius: 6px;
  padding: .2rem .7rem;
  font-size: .85rem;
  font-weight: 600;
}

.project-links {
  justify-content: center;
  gap: .7rem;
  margin-top: .7rem;
  display: flex;
}

.project-btn {
  color: #4e567e;
  background: #d2f1e4;
  border: none;
  border-radius: 5px;
  padding: .4rem 1.1rem;
  font-size: 1rem;
  font-weight: 600;
  text-decoration: none;
  transition: background .2s, color .2s;
}

.project-btn:hover {
  color: #fff;
  background: #4e567e;
}

.theme-toggle {
  z-index: 100;
  color: #4e567e;
  width: 44px;
  height: 44px;
  cursor: pointer;
  background: #fff;
  border: 1px solid #4e567e;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  transition: background .2s, color .2s;
  display: flex;
  position: fixed;
  top: 1.2rem;
  right: 1.2rem;
  box-shadow: 0 2px 8px #00000012;
}

.theme-toggle.dark {
  color: #d2f1e4;
  background: #222;
  border: 1px solid #d2f1e4;
}

:root {
  --card-bg: #fff;
  --main-bg: #f8f9fa;
  --text-color: #222;
}

[data-theme="dark"] {
  --card-bg: #23263a;
  --main-bg: #181a23;
  --text-color: #f5f5f5;
}

body, html {
  background: var(--main-bg);
  color: var(--text-color);
  transition: background .3s, color .3s;
}

@media (max-width: 700px) {
  .portfolio-grid {
    max-width: 98vw;
    grid-template-columns: 1fr;
  }

  .project-image {
    max-width: 100vw;
    height: 160px;
  }

  section.padding {
    padding: 2.5rem 0;
  }
}

img.socialIcon {
  height: 30px;
  width: 30px;
}

